import React,{Component} from "react";
import "../stylesheet/users.css"
//import Users from './users';

export default class Users extends Component{

    // user array list
    state = {
        User : [

              { 
                name: 'Kgaogelo Lekoni',
                surname: 'Sebala',
                location: 'Lithuli',
                age: 31
              },
              {
                name: 'Phinda Futhi',
                surname: 'Mdala',
                location:'Turf',
                age: 45
              },
              {
                name: 'James Rebook',
                surname: 'Jacobs',
                location:'Seshego',
                age: 88
              }
                
         ]
    }

    render() {
        return (
            <>
                <div className="user-cl">
                    <p>Our Users Inline order of Age</p>
                    <ul>
                        <h1>{ this.state.output }</h1>
                            {this.state.User.map(element =><li>{element.name + '    ' + element.surname + ', '  + element.location + ' Age:'+element.age}</li>)}
                    </ul>
                </div>
            </>
        )
    }

}