import React,{Component} from "react";
import "../stylesheet/landing.css";
import res from '../Images/online.png';
import flutter from '../Images/flutter.png';
import rct from '../Images/react.png';
import web from '../Images/web.png';
import scrum from '../Images/scrum.png';

export default class Landing extends Component{
    render() {
        return (
            <>
                <div class="home-container">

                    {/* left side of the home page */}

                    <div className="h-left">

                        {/* loading */}
                        <div className="left-img">
                            <img src={res} alt=""/>
                        </div>

                        {/* welcome*/}
                        <div className="left-welcome">

                            <div className="welcome-div">
                                <h2>Hello, welcome to my resume.</h2>
                            </div>

                            <div className="name-div">
                                <h4>I'm Daniel Mawasha, A Software Developer &amp; A Designer.
                                </h4>
                            </div>

                            <div className="button-div">

                                    <button type="button" className="download-cv">Download CV</button>

                                    <button type="button" className="contact-me">Contact Me</button>
                            </div>

                            <div className="icons-div">
                                
                                <div className="icons">
                                <img src={rct} alt="" className="ic1"/>
                                <h5>React Dev</h5>
                                </div>

                                <div className="icons">
                                <img src={web} alt="" className="ic2"/>
                                <h5>Web Dev</h5>
                                </div>

                                <div className="icons">
                                <img src={scrum} alt="" className="ic3"/>
                                <h5>Scrum Master</h5>
                                </div>

                                <div className="icons">
                                <img src={flutter} alt="" className="ic4"/>
                                <h5>Flutter</h5>
                                </div>

                            </div>

                        </div>

                    </div>

                    {/* right side of the home page */}
                    

                    {/* <p class="hello">Hello,</p>
                    <p class="name">I'm Daniel</p>
                    <p class="profession">I am Software Developer &amp; Designer.</p> */}

                    {/* <!-- buttons --> */}
                    {/* <button type="button" class="download-cv">Download CV</button>
                    <button type="button" class="contact-me">Contact Me</button> */}

                    {/* <div class="about-desc">

                        <h2>ABOUT ME</h2>
                        <h4>SOFTWARE DEVELOPER</h4>

                        <p>Hello, my name is Daniel Mawasha. I'm a software developer graduate at the institution of Tshwane University of Technology. I now look forward to enhance my skills in this field of software development at any institution that offers training
                            and assistance.<br></br> I also acquired skills of graphic design using Adobe Photoshop.
                        </p>

                    </div> */}

                </div>
            </>
        )
    }
}