import React,{useState} from "react";
import "../stylesheet/profile.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCheckSquare, faCoffee} from '@fortawesome/fontawesome-free-solid'


const Profile = () => {
    
        const [usersInfo,setUsersInfo]=useState([]);

        const AddUsersInfo = (first_name,last_name,age,location) =>{
            setUsersInfo([
                ...usersInfo,{
                    uid: usersInfo.length,
                    _name: first_name,
                    _surname: last_name,
                    _age: age,
                    _location: location
                }
            ])
        }

        const [name, setName]=useState('');
        const [surname, setSurname]=useState('');
        const [age, setAge]=useState('');
        const [location, setLocation]=useState('');


        // function to receive data
        const submitInfo = (e) =>{
            e.preventDefault();
            AddUsersInfo(name,surname,age,location)
        }

        const validateName=(name) =>{

            if(name===''){
                return {name} + ' field is required!'; 
            }

            if(name.length<3){
                return {name} + ' name should at least be three characters'
            }
        }

        const validateSurname=surname=>{
            if(surname===''){
                return {surname} + ' field is required!'; 
            }

            if(surname.length<3){
                return {surname} + ' name should at least be three characters'
            }
        }

        const valAge = (age) =>{
            if(age===''){
                return {age} + ' field is required!'
            }else if(age<18){
                return {age} + ' you are too young to register'
            }else{
                return null;
            }
        }

        const valLocation=(location)=>{
            if(location===''){
                return {location} + ' field is required!'
            }else if(location.length<3){
                return {location} + ' location name too short!'
            }else{
                return null;
            }
        }

        const validations={
            _name: name=>validateName(name),
            _surname: surname=>validateSurname(surname),
            _age: age=>valAge(age),
            _location: location=>valLocation(location)
        }

        return (
            <>
                <div className="prof-container">

                    <div className="prof-right">

                        <h5 className="reg-txt">Register Account</h5>

                        <form className="form-wrapper" onSubmit={submitInfo} validations={validations}>

                            <input type="text" placeholder="First-Name" className="input-field"
                                value={name} onChange={(e)=>setName(e.target.value)}
                            />
                            
                            <input type="text" placeholder="Last-Name" className="input-field"
                            value={surname}
                            onChange={(e)=>setSurname(e.target.value)}

                            />
                            
                            <input type="text" placeholder="Age" className="input-field"
                            value={age}
                            onChange={(e)=>setAge(e.target.value)}
                            />

                            <input type="text" placeholder="Location" className="input-field"
                            value={location}
                            onChange={(e)=>setLocation(e.target.value)}
                            />

<div className="submit" onSubmit={submitInfo} type="submit">
                                <button  type="submit">Register</button>
                        </div>
                        </form>

                       

                        {/* details sections */}
                        <div className="contact-details">

                        {/* user entered data */}
                            {usersInfo.map(action => <p> User: {action._name} successfully registered.
                            </p>)
                            }

                           {/* <p>
                            User: {usersInfo.map(action => <p> {action._name}, {action._surname}
                            </p>)
                            } successfully registered.</p> */}

                            {/* {usersInfo.map(action => <p>"try"  {action._name}, {action._surname}
                            </p>)
                            } */}
                            <h3>Contact Details</h3>

                            <ul>

                                <li className="fab">
                                <FontAwesomeIcon icon={["fas", "envelope"]} /> mdmawasha@gmail.com
                                </li>

                                <li className="fab">
                                <FontAwesomeIcon icon={["fas", "phone"]} /> (+27)812767830 | (+27)815734912
                                </li>


                                <li className="fab">
                                <FontAwesomeIcon icon={["fas", "map-pin"]} /> 872 zone f, Seshego Polokwane 
                                </li>

                            </ul>   
                        </div>

                    </div>
                        

                </div>
            </>
        )
    }
export default Profile;