import React,{Component} from "react";
import '../stylesheet/about.css'
import Icon2 from '../Images/icon2.png'


export default class About extends Component{
    render() {
        return (
            <>

                <div className="container-res">

                    {/* resume container */}
                    <div className="res-left">

                        <div className="left-details">

                            <div className="res-circle">
                                <img src={Icon2} alt="" className="doc-icon"/>

                                <div className="contact-me">
                                
                                    <h2 className="headings">About Me</h2>

                                    <p>Hello, my name is Daniel Mawasha. I'm a software developer graduate at the institution of Tshwane University of Technology. I now look forward to enhance my skills in this field of software development at any institution that offers training and assistance.
                                    I also acquired skills of graphic design using Adobe Photoshop.</p>

                                </div>

                            </div>

                            <div className="left-below">
                                
                                


                            </div>

                        </div>                        

                    </div>

                    <div className="res-right">

                        <div className="">

                            

                        </div>

                    </div>
                    
                {/* ending tag of resume */}
                </div>

            </>
        )
    }
}